import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'
export default {
	components: {
		uniCalendar
	},
	data() {
		return {
			shopid: 0,
			name: '',
			num:'',
			people: '',
			showCalendar: false,
			info: {
				date: '',
				startDate: '',
				endDate: '',
				lunar: true,
				range: true,
				insert: false,
				selected: []
			},
			startDate: "",
			endDate: ""
		}
	},
	onLoad: function(option) {
		this.shopid = option.shopid
	},
	methods: {
		open(type) {
			if (type == 'start') {

				this.$refs.calendar.open()
			} else if (type == 'end') {
				this.$refs.calendar1.open()
			}
		},
		confirm(e) {
			console.log('confirm 返回:', e)
			this.startDate = e.fulldate
		},
		confirm1(e) {
			console.log('confirm 返回:', e)
			this.endDate = e.fulldate
		},
		addhongbao(){
			uni.request({
				url:this.$bashUrl+'/index.php/Home/Api/cardadd',
				method:"post",
				data:{
					store_id:this.shopid,
					name:this.name,
					price:this.num,
					type:3,
					createtime:this.startDate,
					endtime:this.endDate
				},
				success: (res) => {
					if(res){
						uni.showToast({
							title:'新建成功',
							success:res=>{
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},2000)
							}
						})
					}else{
						uni.showToast({
							title:'新建失败，请稍后重试',
							icon:'none'
						})
					}
				}
			})
		}
	}
}
